<template>
  <div id="app">
    <HeaderComp />
    <TopBanner :item="topBanner" />
    <TagsChecked v-for="tag in tagsList" :tag="tag" :key="tag.id" @selectedTag="selectTag" />
    <p> {{ selected }}</p>
    <ProjectsComp :projects="filteredProjects" />






    <FooterComp />

  </div>
</template>

<script>
import ProjectsComp from './components/ProjectsComp.vue';
import HeaderComp from './components/Header.vue';
import FooterComp from './components/Footer.vue';
import TopBanner from './components/TopBanner.vue';
import TagsChecked from './components/TagsChecked.vue';

export default {
  name: 'App',
  components: {
    HeaderComp,
    TopBanner,
    FooterComp,
    ProjectsComp,
    TagsChecked
  },
  data() {
    return {
      tagsList: [
        { id: 0, value: "ShowAll" },
        { id: 1, value: "Bathroom" },
        { id: 2, value: "Bed Room" },
        { id: 3, value: "Kitchen" },
        { id: 4, value: "Living Area" }
      ],

      projects: [
        {
          id: 1,
          title: "Minimal Bedroom",
          subtitle: "Decor / Artchitecture",
          tag: "Bathroom",
          src: "project1.jpg"
        },
        {
          id: 2,
          title: "Minimal Bedroom",
          subtitle: "Decor / Artchitecture",
          tag: "Bedroom",
          src: "project2.jpg"
        },
        {
          id: 3,
          title: "Classic Minimal Bedroom",
          subtitle: "Decor / Artchitecture",
          tag: "Bathroom",
          src: "project3.jpg"
        },
        {
          id: 4,
          title: "Modern Bedroom",
          subtitle: "Decor / Artchitecture",
          tag: "Bedroom",
          src: "project4.jpg"
        },
        {
          id: 5,
          title: "Minimal Bedroom table",
          subtitle: "Decor / Artchitecture",
          tag: "Kitchen",
          src: "project5.jpg"
        },
        {
          id: 6,
          title: "System Table",
          subtitle: "Decor / Artchitecture",
          tag: "Kitchen",
          src: "project6.jpg"
        },
        {
          id: 7,
          title: "Modern Bedroom",
          subtitle: "Decor / Artchitecture",
          tag: "Bedroom",
          src: "project7.jpg"
        },
        {
          id: 8,
          title: "Modern Bedroom",
          subtitle: "Decor / Artchitecture",
          tag: "Living Area",
          src: "project8.jpg"
        },
      ],

      topBanner:
      {
        id: 1, src: "./../ assets / top_banner_Projects.jpg", title: "Our Project", subtitle: "Home / Project"
      },

      selected: "",
      sortedProjects: []
    };


  },
  computed: {
    filteredProjects() {
      if (this.sortedProjects.length) {
        return this.sortedProjects
      } else {
        return this.projects
      }
    }
  },
  methods: {
    selectTag(tag) {
      //this.selected = tag.value
      this.sortedProjects = [];
      let vm = this;
      this.projects.map(function (item) {
        if (item.tag === tag.value) {
          vm.sortedProjects.push(item)
        }
      })

    }
  }


};
</script>

